@use "../abstract/mixins"as m;
@use "../abstract/variables"as v;

.auth-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-width: 800px;
    width: 100%;
    padding: 2rem;
    background-color: v.$white;
    border-radius: 1rem;
    box-shadow: v.$shadow-l;

    @include m.mobile {
        gap: 1.5rem;
        padding: 3rem;
    }

    @include m.tablet {
        gap: 2rem;
        padding: 4rem;
    }

    @include m.desktop {
        gap: 2.5rem;
        padding: 5rem;
    }

    .field {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;

        label {
            color: v.$gray-500;
            font-weight: 400;
            font-size: 1.4rem;

            @include m.desktop {
                font-size: 1.6rem;
            }
        }

        input:not([type="submit"]) {
            background-color: v.$gray-100;
            border: 2px solid v.$gray-100;
            padding: 0.7rem 1.2rem;
            color: v.$gray-600;
            font-weight: 300;
            font-size: 1.4rem;
            border-radius: 4px;
            transition: border .3s ease;

            @include m.mobile {
                padding: 0.8rem 1.3rem;
            }

            @include m.tablet {
                padding: 0.9rem 1.4rem;
            }

            @include m.desktop {
                padding: 1rem 1.5rem;
                font-size: 1.6rem;
            }

            &:focus {
                outline: none;
                border: 2px solid v.$indigo-600;
            }
        }

        .error {
            font-size: 1.2rem;
            font-weight: 300;
            color: v.$red-700;

            @include m.tablet {
                font-size: 1.4rem;
            }
        }

        &.actions {
            flex-direction: row;
            justify-content: space-between;

            a {
                padding: 1rem 0;
                font-size: 1.3rem;
                font-weight: 300;
                color: v.$gray-500;
                transition: color .3s ease;

                @include m.mobile {
                    font-size: 1.4rem;
                }

                &:hover {
                    color: v.$gray-600;
                }
            }
        }

        &.buttons {
            padding-top: 1rem;

            @include m.tablet {
                align-items: center;
            }
        }
    }
}

.confirm {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    max-width: 800px;
    width: 100%;
    padding: 2rem;
    background-color: v.$white;
    border-radius: 1rem;
    font-size: 1.6rem;
    color: v.$gray-600;
    box-shadow: v.$shadow-l;

    @include m.mobile {
        gap: 1.5rem;
        padding: 3rem;
    }

    @include m.tablet {
        gap: 2rem;
        padding: 4rem;
        font-size: 1.8rem;
    }

    @include m.desktop {
        gap: 2.5rem;
        padding: 5rem;
    }
}