@use "../abstract/variables"as v;
@use "../abstract/mixins"as m;

.primary-btn {
    @include m.btn(500);
    background-color: v.$indigo-600;
    color: v.$white;
    border: 2px solid v.$indigo-600;

    &:hover {
        background-color: v.$indigo-500;
    }
}

.secondary-btn {
    @include m.btn(500);
    background-color: v.$transparent;
    color: v.$indigo-600;
    border: 2px solid v.$indigo-600;

    &:hover {
        background-color: v.$transparent;
        color: v.$indigo-500;
        border-color: v.$indigo-500;
    }
}

.tertiary-btn {
    @include m.btn(500);
    background-color: v.$transparent;
    color: v.$indigo-100;
    border: 2px solid v.$indigo-100;

    &:hover {
        background-color: v.$transparent;
        color: v.$white;
        border-color: v.$white;
    }
}