@use "../abstract/mixins"as m;
@use "../abstract/variables"as v;

main {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    max-width: 1200px;
    width: 95%;
    margin: 0 auto;

    @include m.mobile {
        gap: 2rem;
    }

    @include m.tablet {
        gap: 3rem;
    }

    @include m.desktop {
        gap: 4rem;
    }


    .title {
        color: v.$gray-700;
        font-size: 2rem;
        font-weight: 600;
        text-align: center;
        letter-spacing: -1px;

        @include m.mobile {
            font-size: 2.4rem;
        }

        @include m.tablet {
            letter-spacing: -2px;
            font-size: 2.8rem;
        }

        @include m.desktop {
            font-size: 3.2rem;
        }
    }

    .subtitle {
        color: v.$gray-500;
        font-size: 1.8rem;
        font-weight: 300;
        text-align: center;
        letter-spacing: -1px;

        @include m.mobile {
            font-size: 2rem;
        }

        @include m.tablet {
            font-size: 2.2rem;
        }

        @include m.desktop {
            font-size: 2.4rem;
        }
    }
}