@use "../abstract/mixins"as m;

.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 500;
    overflow-y: auto;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .25);
    animation: fadeIn .3s ease;

    &.fade-out {
        animation: fadeOut .3s ease forwards;
    }

    .content {
        display: flex;
        flex-direction: column;
        max-width: 600px;
        width: 95%;
        margin: auto;
        background-color: #ffffff;
        border-radius: 6px;
        animation: slideInTop .3s ease forwards;

        @include m.tablet {
            width: 100%;
        }

        &.slide-out-top {
            animation: slideOutTop .3s ease forwards;
        }

        .header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 50px;
            padding: 0 14px;
            border-bottom: 1px solid #eeeeee;

            @include m.mobile {
                height: 60px;
                padding: 0 16px;
            }

            @include m.tablet {
                height: 70px;
                padding: 0 18px;
            }

            @include m.desktop {
                padding: 0 22px;
            }

            .title {
                font-size: 1.6rem;
                font-weight: 300;

                @include m.mobile {
                    font-size: 1.8rem;
                }

                @include m.tablet {
                    font-size: 2rem;
                }
            }

            .close {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 40px;
                height: 40px;
                margin-right: -10px;
                color: #888888;
                transition: color .3s ease;

                &:hover {
                    color: #3b82f6;
                }

                svg {
                    width: 24px;
                    height: 24px;
                }
            }
        }

        .body {
            display: flex;
            padding: 14px;

            @include m.mobile {
                padding: 16px;
            }

            @include m.tablet {
                padding: 18px;
            }

            @include m.desktop {
                padding: 22px;
            }
        }

        .footer {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            gap: 20px;
            height: 60px;
            padding: 0 14px;
            border-top: 1px solid #eeeeee;

            @include m.mobile {
                height: 70px;
                padding: 0 16px;
            }

            @include m.tablet {
                padding: 0 18px;
            }

            @include m.desktop {
                height: 80px;
                padding: 0 22px;
            }
        }
    }
}

