@use "../abstract/mixins"as m;
@use "../abstract/variables"as v;

.chat {
    display: flex;
    overflow: hidden;
    max-width: 1200px;
    width: 100%;
    height: 800px;
    border-radius: 1rem;
    box-shadow: v.$shadow-l;

    .sidebar {
        flex: 1 1 30%;
        background-color: v.$slate-700;
    }

    .main {
        display: flex;
        flex-direction: column;
        flex: 1 1 70%;

        .header {
            display: flex;
            align-items: center;
            min-height: 50px;
            padding: 0 1.5rem;
            background-color: v.$white;

            @include m.mobile {
                min-height: 55px;
                padding: 0 2rem;
            }

            @include m.tablet {
                min-height: 60px;
                padding: 0 2.5rem;
            }

            @include m.desktop {
                min-height: 70px;
                padding: 0 3rem;
            }

            p {
                font-size: 1.6rem;
            }
        }

        .messages {
            display: flex;
            flex-direction: column;
            gap: 1rem;
            overflow-y: scroll;
            height: auto;
            padding: 1rem;
            background-color: v.$slate-100;
            font-size: 1.4rem;

            @include m.mobile {
                gap: 1.5rem;
                padding: 1.5rem;
            }

            @include m.tablet {
                gap: 2rem;
                padding: 2rem;
            }

            @include m.desktop {
                gap: 3rem;
                padding: 3rem;
            }

            &::-webkit-scrollbar {
                width: 10px;
                height: 10px;
                background-color: transparent;
            }

            &::-webkit-scrollbar-corner {
                background-color: transparent;
                border-color: transparent;
            }

            &::-webkit-scrollbar-thumb {
                border-radius: 10px;
                background-color: v.$slate-300;
            }

            &::-webkit-scrollbar-track {
                border-radius: 10px;
                background-color: transparent;
            }

            .reply,
            .sent {
                display: flex;
                max-width: 500px;
                padding: 1rem 1.5rem;
                border-radius: 2rem;
                font-size: 1.3rem;
    
                @include m.desktop {
                    padding: 1.5rem 2rem;
                    font-size: 1.4rem;
                }
            }

            .reply {
                align-self: flex-start;
                background-color: v.$slate-600;
                color: v.$white;;
            }

            .sent {
                align-self: flex-end;
                background-color: v.$white;
            }

            
        }

        .message-form {
            display: flex;
            position: relative;
            min-height: 50px;
            background-color: v.$white;

            @include m.mobile {
                min-height: 55px;
            }

            @include m.tablet {
                min-height: 60px;
            }

            @include m.desktop {
                min-height: 70px;
            }

            label {
                position: absolute;
                top: 0;
                left: 0;
            }

            input[type="text"] {
                z-index: 1;
                position: relative;
                width: 100%;
                height: 100%;
                padding: 0 1.5rem;
                background-color: v.$white;
                border: none;
                color: v.$gray-600;
                font-weight: 300;
                font-size: 1.4rem;

                &:focus {
                    outline: none;
                }

                @include m.mobile {
                    padding: 0 2rem;
                }
    
                @include m.tablet {
                    padding: 0 2.5rem;
                }

                @include m.desktop {
                    padding: 0 3rem;
                    font-size: 1.6rem;
                }
            }

            button {
                display: flex;
                justify-content: center;
                align-items: center;
                min-width: 60px;
                background-color: v.$slate-600;
                transition: background-color .3s ease;

                @include m.mobile {
                    min-width: 70px;
                }
    
                @include m.tablet {
                    min-width: 80px;
                }

                @include m.desktop {
                    min-width: 90px;
                }

                &:hover {
                    background-color: v.$slate-600;
                }

                svg {
                    width: 24px;
                    height: 24px;
                    color: v.$white;
                }
            }
        }
    }
}