@use "../abstract/variables"as v;

html {
    height: 100%;
    box-sizing: border-box;
    font-size: 62.5%;
}

*,
*::before,
*::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
    font: inherit;
}

body {
    height: 100%;
    font-family: v.$font;
    font-size: 1.6rem;
    background-color: v.$slate-50;
    color: v.$gray-800;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
}

button {
    border: none;
    background-color: transparent;
    cursor: pointer;
}