$font: "Poppins", sans-serif;

$mobile: 480px;
$tablet: 768px;
$desktop: 1024px;
$desktopXL: 1200px;

$transparent: transparent;
$white: rgb(255 255 255);
$black: rgb(0 0 0);

$gray-50: rgb(249 250 251);
$gray-100: rgb(243 244 246);
