$slate-600: rgb(71 85 105);
$slate-700: rgb(51 65 85);
$slate-800: rgb(30 41 59);
$slate-900: rgb(15 23 42);

$indigo-50: rgb(238 242 255);
$indigo-100: rgb(224 231 255);
$indigo-200: rgb(199 210 254);
