@use "variables"as v;

@mixin mobile {
    @media (min-width: v.$mobile) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}

@mixin grid($columns, $space) {
    display: grid;
    grid-template-columns: repeat($columns, 1fr);
    gap: $space;
}

@mixin reset-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin btn($weight) {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    flex-grow: 1;
    padding: 1rem 2.5rem;
    font-size: 1.4rem;
    font-weight: $weight;
    border-radius: 4px;
    transition: background-color, color, border, .3s ease;

    @include mobile() {
        flex-grow: 0;
        padding: 1rem 3rem;
    }

    @include tablet {
        padding: 1.2rem 3.5rem;
    }

    @include desktop {
        font-size: 1.6rem;
    }

    &.hidden {
        display: none;
    }
}